import {
  SET_MOOD,
  SET_USERS,
  SET_TOKEN,
} from '../actions/app';

const INITIAL_STATE = {
  mood: 50,
  users: 0,
  token: null,
  tokenExpires: 0,
};

export default function appReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case SET_MOOD:
      return {
        ...state,
        mood: action.mood,
      };

    case SET_USERS:
      return {
        ...state,
        users: action.users,
      };

    case SET_TOKEN:
      return {
        ...state,
        token: action.token,
        tokenExpires: action.tokenExpires,
      };

    default:
      return state;
  }
}



