import { API_URL } from '../utils/api';

export const SET_MOOD = 'SET_MOOD';
export const SET_USERS = 'SET_USERS';
export const SET_TOKEN = 'SET_TOKEN';

export const setMood = (mood = 50) => (dispatch, getState) => {
  const { app: {token}, party: { partyId } } = getState();
  // TODO: This should be a POST request
  const url = `${API_URL}/mood?partyId=${partyId}&mood=${mood}&token=${token}`;
  const promise = fetch(url)
    .then(response => response.json());
  dispatch({
    type: SET_MOOD,
    mood,
  });
  return promise;
};

export function setUsers(users = 0) {
  return {
    type: SET_USERS,
    users,
  };
}

export const setToken = token => ({
  type: SET_TOKEN,
  token,
  tokenExpires: Date.now() + (60 * 60 * 1000),
});



