import React, { Component } from 'react';
import { connect } from 'react-redux';
import Slider from 'rc-slider/lib/Slider';
import 'rc-slider/assets/index.css';
import './MoodSlider.css';
import { setMood } from '../actions/app';
import clubIcon from '../party-dj.svg';
import loungeIcon from '../lounge-chair.svg';

class MoodSlider extends Component {
  render() {
    return (
      <div className="mood-slider">
        <div className="mood">
          <img className="mood-icon" src={ loungeIcon } />
          <div className="mood-text">
            <span className="text-purple-red">Lounge</span>
          </div>
        </div>
        <div className="slider">
          <Slider
            defaultValue={ this.props.mood }
            onAfterChange={ this.props.setMood }
          />
          <div className="mood-text">&nbsp;</div>
        </div>
        <div className="mood">
          <img className="mood-icon" src={ clubIcon } />
          <div className="mood-text">
            <span className="text-purple-red">Club</span>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(
  ({ app: { mood } }) => ({
    mood,
  }),
  dispatch => ({
    setMood: mood => dispatch(setMood(mood)),
  })
)(MoodSlider);



