import { API_URL } from '../utils/api';

export const UPDATE_PARTY = 'UPDATE_PARTY';

export const updateParty = ({ partyId, playlistId, ready }) => ({
  type: UPDATE_PARTY,
  partyId,
  playlistId,
  ready,
});

export const hostParty = () => (dispatch, getState) => {
  const { app: { token } } = getState();
  return fetch(`${API_URL}/host_party?token=${token}`)
    .then(response => response.json())
    .then(data => {
      const { partyId, playlistId } = data;
      dispatch(updateParty({ partyId, playlistId, ready: true }));
      return data;
    });
};

export const joinParty = partyId => (dispatch, getState) => {
  const { app: { token } } = getState();
  return fetch(`${API_URL}/join_party/${partyId}?token=${token}`)
    .then(response => {
      dispatch(updateParty({ partyId, playlistId: null, ready: true }));
      return response;
    });
};



