import { UPDATE_PARTY } from '../actions/party';

const INITIAL_STATE = {
  partyId: null,
  playlistId: null,
  ready: false,
};

export default function partyReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case UPDATE_PARTY:
      return {
        ...state,
        partyId: action.partyId,
        playlistId: action.playlistId,
        ready: action.ready,
      };

    default:
      return state;
  }
}



