import React, { Component } from 'react';
import { connect } from 'react-redux';
import './Code.css';

class Code extends Component {
  render() {
    return (
      <div className="code-component">
        <div className="code-title">
          <span className="text-purple-red">
            Give this code to your friends
          </span>
        </div>
        <div className="code-code">
          <span className="text-purple-red">
            { this.props.partyId }
          </span>
        </div>
      </div>
    );
  }
}

export default connect(({ party: { partyId } }) => ({ partyId }))(Code);



