import React, { Component } from 'react';
import AppTitle from './AppTitle';
import { connect } from 'react-redux';
import { joinParty } from '../actions/party';

class Guest extends Component {
  constructor(props) {
    super();
    this.state = {
      code: '',
    };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange = event => this.setState({ code: event.target.value });
  handleClick = () => this.props.joinParty(this.state.code);

  render() {
    return (
      <div>
        <AppTitle />
        <input
          type="text"
          value={ this.state.value }
          onChange={ this.handleChange }
        />
        <button type="button" onClick={ this.handleClick }>Join</button>
      </div>
    );
  }
}

export default connect(
  () => ({}),
  dispatch => ({
    joinParty: code => dispatch(joinParty(code)),
  }),
)(Guest);



