import React, { Component } from 'react';
import { connect } from 'react-redux';
import './Host.css';
import MoodSlider from './MoodSlider';
import Code from './Code';
import Users from './Users';
import AppTitle from './AppTitle';
import { hostParty } from '../actions/party';
import { play } from '../actions/player';

class Host extends Component {
  componentDidMount() {
    this.props.hostParty();
  }

  render() {
    return (
      <div className="host-component container">
        <header className="host-header">
          <Users />
        </header>

        <div className="host-flex">
          <AppTitle />
        </div>

        <div className="host-flex">
          <MoodSlider />
        </div>

        <div className="host-flex text-center">
          <button
            type="button"
            className="btn btn-display btn-purple-red clickable"
            onClick={ this.props.play }
          >
            Play
          </button>
        </div>

        <div className="host-flex">
          <Code />
        </div>
      </div>
    );
  }
}

export default connect(
  () => ({}),
  dispatch => ({
    hostParty: () => dispatch(hostParty()),
    play: () => dispatch(play()),
  }),
)(Host);



