/*eslint no-console: "off"*/
/*global Spotify:true*/

let player;

export const play = (contextUri) => (dispatch, getState) => {
  const { app: { token }, party: { playlistId, ready } } = getState();
  if (ready) {
    fetch('https://api.spotify.com/v1/me/player/play', {
      method: 'PUT',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization': 'Bearer ' + token,
      },
      body: JSON.stringify({
        'context_uri': 'spotify:user:marcus0706:playlist:' + playlistId,
      }),
    });
  }
};

export const togglePlay = () => dispatch => {
  player.togglePlay();
};

export const playbackReady = () => (dispatch, getState) => {
  const { app: { token } } = getState();
  player = new Spotify.Player({
    name: 'Junction 🇫🇮',
    getOAuthToken: cb => cb(token),
  });

  // Error handling
  player.on('initialization_error', e => console.log('Initialization Error', e));
  player.on('authentication_error', e => console.log('Authentication Error', e));
  player.on('account_error', e => console.log('Account Error', e));
  player.on('playback_error', e => console.log('Playback Error', e));

  // Playback status updates
  player.on('player_state_changed', e => {
    console.log(e.track_window.current_track);
    // updateCurrentTrack(e.track_window.current_track)
  });

  // // Ready
  // player.on('ready', data => transferPlayback(data.device_id));

  // Connect to the player!
  player.connect();
  console.log(player);
  console.log(player.getCurrentState());
};



