let token = null;

export default function spotifyAuth(redirect = 'host') {
  const hash = window.location.hash
    .substring(1)
    .split('&')
    .reduce((initial, item) => {
      if (item) {
        var parts = item.split('=');
        initial[parts[0]] = decodeURIComponent(parts[1]);
      }
      return initial;
    }, {});
  window.location.hash = '';

  // Set token
  token = hash.access_token;

  const authEndpoint = 'https://accounts.spotify.com/authorize';

  // Replace with your app's client ID, redirect URI and desired scopes
  const clientId = 'a9b4c32e2d98436fbf23777eafc3a300';
  const scopes = [
    'streaming',
    'user-modify-playback-state',
    'user-read-birthdate',
    'user-read-email',
    'user-read-private',
    'user-top-read',
    'playlist-modify-private',
  ];

  if (token) {
    return token;
  } else {
    window.location = `${authEndpoint}?client_id=${clientId}&redirect_uri=http://localhost:3000/${redirect}&scope=${scopes.join('%20')}&response_type=token`;
    return null;
  }
}



